/* Mobile Off canvas */
.off-canvas-wrapper {
  top: 0;
  right: 0;
  height: 100vh;
  position: fixed;
  background-color: #fff;
  z-index: 9999;
  transition: 0.4s;
  transform: translateX(100%);
  box-shadow: 0 0 87px 0 rgba(0, 0, 0, 0.09);
  opacity: 0;
  visibility: hidden;
  width: 250px;
}

@media (min-width: 480px) {
  .off-canvas-wrapper {
    width: 300px;
  }
}

.off-canvas-wrapper.open {
  transform: translateX(0);
  opacity: 1;
  visibility: visible;
}

.off-canvas-wrapper .off-canvas-inner {
  padding-top: 30px;
  padding-left: 20px;
  padding-right: 20px;
  height: 100%;
  position: relative;
  overflow-y: scroll;
  margin-top: 1px;
  margin-left: 1px;
}

.off-canvas-wrapper .btn-close-off-canvas {
  top: 0;
  left: -50px;
  width: 50px;
  height: 50px;
  font-size: 30px;
  background-color: #62ab00;
  color: #fff;
  line-height: 50px;
  text-align: center;
  cursor: pointer;
  position: absolute;
  -webkit-transition: 0.4s;
  -o-transition: 0.4s;
  transition: 0.4s;
}

.off-canvas-wrapper .search-box.offcanvas {
  position: relative;
}

.off-canvas-wrapper .search-box.offcanvas input {
  width: 100%;
  height: 40px;
  padding-right: 50px;
  text-indent: 15px;
}

.off-canvas-wrapper .search-box.offcanvas input:focus {
  box-shadow: none;
  outline: none;
}

.off-canvas-wrapper .search-box.offcanvas button {
  width: 40px;
  height: 100%;
  position: absolute;
  top: 0;
  right: 0;
  font-size: 21px;
  background: #62ab00;
  color: #fff;
  display: flex;
  align-items: center;
  justify-content: center;
}

.off-canvas-wrapper .menu-expand {
  display: none;
}

.off-canvas-wrapper .header-social .single-icon {
  padding: 0 8px;
}

.off-canvas-wrapper .menu-block-2 > li {
  margin-bottom: 5px;
}

.off-canvas-wrapper .menu-block-2 > li:hover > a {
  color: #62ab00 !important;
}

