  font-size: 14px;
  font-weight: 400;
  color: #14191e;
  margin: 0;
}

.checkout-form .check-box input[type="checkbox"] + label::before {
  position: absolute;
  left: 0;
  top: 0;
  width: 20px;
  height: 20px;
  display: block;
  border: 2px solid #565f69;
  content: "";
  transition: all 0.3s ease 0s;
}

