/*>>>>>>>> Login-register <<<<<<<<<*/
/*-- Login Title --*/
.login-title {
  line-height: 23px;
  text-transform: capitalize;
  font-weight: 500;
  margin-bottom: 18px;
  color: #333;
  font-weight: 600;
}

@media (min-width: 768px) {
  .login-title {
    font-size: 27px;
  }
}

/*-- Login Form --*/
.login-box {
  border: 1px solid #e3e3e3;
  padding: 45px 20px 20px 20px;
  text-align: left;
  border-radius: 5px;
}

.login-box .btn {
  text-transform: capitalize;
  font-size: 15px;
}

.login-form {
  border: 1px solid #e3e3e3;
  padding: 45px 20px 20px 20px;
  text-align: left;
  border-radius: 5px;
}

@media (min-width: 768px) and (max-width: 991px) {
  .login-form {
    padding: 15px;
  }
}

.login-form .btn {
  text-transform: capitalize;
  font-size: 15px;
}

.login-form label {
  display: block;
  font-size: 13px;
  margin-bottom: 8px;
  font-weight: 400;
  text-transform: capitalize;
}

.login-form .nice-select {
  width: 100%;
  background-color: transparent;
  border: 1px solid #565f69;
  border-radius: 0;
  line-height: 23px;
  padding: 10px 20px;
  font-size: 14px;
  height: 45px;
  color: #d4d4d4;
  margin-bottom: 15px;
}

.login-form .nice-select::after {
  width: 6px;
  height: 6px;
  border-width: 1px;
  right: 20px;
  border-color: #565f69;
}

.login-form .nice-select .current {
  color: #555555;
  display: block;
  line-height: 23px;
}

.login-form .nice-select .list {
  width: 100%;
}

.login-form input {
  height: 40px;
  color: #7b7b7b;
  font-size: 13px;
}

.login-form input:focus {
  border-color: #62ab00;
  box-shadow: none;
  outline: none;
}

.login-form .check-box {
  float: left;
  margin-right: 70px;
}

.login-form .check-box:last-child {
  margin-right: 0;
}

.login-form .check-box input[type="checkbox"] {
  display: none;
}

.login-form .check-box input[type="checkbox"] + label {
  position: relative;
  padding-left: 30px;
  line-height: 20px;
  font-size: 14px;
  font-weight: 400;
  color: #d4d4d4;
  margin: 0;
}

.login-form .check-box input[type="checkbox"] + label::before {
  position: absolute;
  left: 0;
  top: 0;
  width: 20px;
  height: 20px;
  display: block;
  border: 2px solid #565f69;
  content: "";
  transition: all 0.3s ease 0s;
}

.login-form .check-box input[type="checkbox"] + label::after {
  position: absolute;
  left: 0;
  top: 0;
  display: block;
  content: "\f00c";
  font-family: Fontawesome;
  font-size: 12px;
  line-height: 20px;
  opacity: 0;
  color: #d4d4d4;
  width: 20px;
  text-align: center;
  transition: all 0.3s ease 0s;
}

.login-form .check-box input[type="checkbox"]:checked + label::before {
  border: 2px solid #565f69;
}

.login-form .check-box input[type="checkbox"]:checked + label::after {
  opacity: 1;
}

.login-form .forget-pass {
  color: #343434;
  font-size: 14px;
}

/*-- Place Order --*/
.register-button {
  display: block;
  margin-top: 40px;
  width: 140px;
  border-radius: 0;
  height: 36px;
  border: none;
  line-height: 24px;
  padding: 6px 20px;
  float: left;
  font-weight: 400;
  text-transform: uppercase;
  color: #fff;
  border-radius: 3px;
}

